const state={
    isLogin:false
}
const getters={
    isLogin:state=>state.isLogin
}
const mutations={
    userStatus(state,user){
        if(user){
            state.currentUser=user
            state.isLogin=true
        }
        else{
            state.currentUser=user
            state.isLogin=false
        }
    }
}
const actions={
    
}
export default{
    state,
    getters,
    mutations,
    actions
}


