//一级路由
import Home from './components/Home'
import Menu from './components/Menu'
import Admin from './components/Admin'
import About from './components/about/About'
import Login from './components/Login'
import Register from './components/Register'

//二级路由
import Contact from './components/about/Contact'
import Delivery from './components/about/Delivery'
import History from './components/about/History'
import OrderingGuide from './components/about/OrderingGuide'

//三级路由
import PersonName from './components/about/contact/PersonName'
import Phone from './components/about/contact/Phone'

export const routes=[
    {path:'/',components:{
        default:Home,
        'orderingGuide':OrderingGuide,
        'delivery':Delivery,
        'history':History
    },name:"homeLink"},
    {path:'/menu',component:Menu,name:"menuLink"},
    {path:'/admin',component:Admin},
    {path:'/about',component:About,redirect:'/history',children:[
      {path:'/history',component:History,name:"historyLink"},
      {path:'/delivery',component:Delivery,name:"deliveryLink"},
      {path:'/contact',component:Contact,name:"contactLink",redirect:'/personName',children:[
        {path:'/personName',component:PersonName,name:"personName"},
        {path:'/phone',component:Phone,name:"phoneNumber"}
      ]},
      {path:'/ordingGuide',component:OrderingGuide,name:"orderingGuideLink"},
    ]},
    {path:'/login',component:Login,name:"loginLink"},
    {path:'/register',component:Register},
    {path:'*',redirect:'/'}
  ]
  


