<template>
    <div class="row mt-3">
        <div class="col-md-12 col-lg-12">
            <div class="card">
                <div class="card-body">
                    <img src="../assets/icon.png" alt="图标" class="mx-auto d-block">
                    <form @submit.prevent="onSubmit">
                        <div class="form-group">
                            <label for="email">邮箱</label>
                            <input type="email" class="form-control" v-model="email"/>
                        </div>
                        <div class="form-group">
                            <label for="password">密码</label>
                            <input type="password" class="form-control" v-model="password"/>
                        </div>
                        <div class="form-group">
                            <label for="confirm-password">确认密码</label>
                            <input type="password" class="form-control" v-model="confirmPassword"/>
                        </div>
                        <button class="btn btn-block btn-success">注册</button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data(){
        return{
            email:'',
            password:'',
            confirmPassword:''
        }
    },
    methods:{
        onSubmit(){
            // if(this.password===this.confirmPassword){
            //     const formData={
            //         email:this.email,
            //         password:this.password,
            //         confirmPassword:this.confirmPassword
            //     }

            //     this.http.post('/users.json',formData).then(res=>this.$router.push({name:'loginLink'}))
            // }
            // else{
            //     alert("两次密码不一致！请重新输入。")
            // }
            this.http.get('/users.json').then(res=>{
            const data=res.data
            const users=[]
            for(let key in data){
                const user=data[key]
                users.push(user)
            }
            //实现过滤
            let result=users.filter((user)=>{
                return user.email===this.email
            })
            //判断result的长度是否大于0
            if(result!=null&&result.length>0){
                alert("这个邮箱已经存在！请重新输入。")
            }
            else{
                if(this.password===this.confirmPassword){
                    const formData={
                        email:this.email,
                        password:this.password,
                        confirmPassword:this.confirmPassword
                    }

                    this.http.post('/users.json',formData).then(res=>this.$router.push({name:'loginLink'}))
                }
                else{
                    alert("两次密码不一致！请重新输入。")
                }
            }
            })
        }
    }
}
</script>




